export default function createLoader() {
    const loader = document.createElement('div');
    loader.id = 'loader';
    for (let i = 0; i < 3; i++) {
        const bounce = document.createElement('div');
        bounce.className = `bounce${i + 1}`;
        loader.append(bounce);
    }
    document.body.append(loader);
    return loader;
}
