export const normalFinger = ({ canvas, scale = 1, x = 0, y = 0, opacity = 1 }) => {
    const g = canvas
        .g()
        .transform('t' + [x, y])
        .attr({ opacity });
    makeHandImage(0)
        .use()
        .transform('matrix(' + scale + ' 0 0 ' + scale + ' 0 0)')
        .appendTo(g);
    return g;
};
export const pressFinger = ({ canvas, scale = 1, x = 0, y = 0, opacity = 0 }) => {
    const g = canvas
        .g()
        .transform('t' + [x, y])
        .attr({ opacity });
    g.circle(2 * scale, 10 * scale, 6 * scale).attr({
        fill: '#f274a5',
        stroke: 'none'
    });
    g.circle(2 * scale, 10 * scale, 10 * scale).attr({
        fill: 'none',
        stroke: '#f69bbe',
        'stroke-width': 3 * scale
    });
    g.circle(2 * scale, 10 * scale, 15 * scale).attr({
        fill: 'none',
        stroke: '#f6ccdc',
        'stroke-width': 3 * scale
    });
    makeHandImage(1)
        .use()
        .transform('matrix(' + scale + ' 0 0 ' + scale + ' ' + -1 * scale + ' ' + 5 * scale + ')')
        .appendTo(g);
    return g;
};
export default function hint({ canvas, scale = 1, type = 0, XY, dragTime = 1000, pathStr, moveTo }) {
    var hand = canvas.g();
    var interval;

    if (type === 0) {
        var img = [];
        var n = 0;
        hand.transform('t' + XY).attr({
            // 't-15,-12s0.6'
            'pointer-events': 'none'
        });
        img[0] = normalFinger({ canvas: hand, scale });
        img[1] = pressFinger({ canvas: hand, scale });

        interval = window.setInterval(function () {
            var val = n % 2;
            if (!val) console.log('interval');
            img.forEach(function (el, i) {
                el.attr({
                    opacity: i ? 1 - val : val
                });
            });
            n += 1;
        }, 500);
    } else if (type === 1) {
        makeHandImage(0)
            .use()
            .transform('matrix(' + scale + ' 0 0 ' + scale + ' 0 0)')
            .appendTo(hand)
            .attr({
                'pointer-events': 'none'
            });
        var startPoint = XY;
        if (pathStr) {
            var myPoint = Snap.path.getPointAtLength(pathStr, 0);
            startPoint = [myPoint.x - 19.2 * scale, myPoint.y - 19.2 * scale];
        }
        hand.attr({
            transform: 't' + startPoint
        });
        setTimeout(myAnim, 300);
        interval = window.setInterval(function () {
            console.log('interval');
            hand.attr({
                transform: 't' + startPoint
            });
            setTimeout(myAnim, 300);
        }, dragTime + 800);
    }

    function myAnim() {
        if (pathStr) {
            new AnimateElement({
                el: hand,
                pathStr: pathStr,
                time: dragTime
            }).start();
        } else {
            hand.animate(
                {
                    transform: 't' + moveTo
                },
                dragTime
            );
        }
    }

    hand.removeHint = () => {
        hand.remove();
        clearInterval(interval);
    };
    return hand;
}
