import { alignGroup } from '.';
import Snap from '../snap';
import '../assets/styles/common.css';
const supportTouch = !!navigator.maxTouchPoints;
(function () {
    Object.defineProperty(Array.prototype, 'first', {
        get() {
            return this[0];
        },
        set(x) {
            this[0] = x;
        }
    });
    Object.defineProperty(Array.prototype, 'last', {
        get() {
            return this[this.length - 1];
        },
        set(x) {
            this[this.length - 1] = x;
        }
    });
    Array.prototype.unique = function () {
        return this.filter((val, i, arr) => arr.indexOf(val) === i);
    };

    Snap.plugin(function (Snap, Element) {
        Element.prototype.anchor = function (x, y) {
            const { localMatrix } = this.transform();
            var elX;
            var elY;
            var moveX = 0,
                moveY = 0,
                BBox = this.getBBox();
            if (this.type === 'circle') {
                elX = BBox.cx;
                elY = BBox.cy;
            } else {
                elX = BBox.x;
                elY = BBox.y;
            }
            switch (x) {
                case 'left':
                    moveX = elX - BBox.x;
                    break;
                case 'center':
                    moveX = elX - BBox.cx;
                    break;
                case 'right':
                    moveX = elX - BBox.x2;
                    break;
            }
            switch (y) {
                case 'top':
                    moveY = elY - BBox.y;
                    break;
                case 'middle':
                    moveY = elY - BBox.cy;
                    break;
                case 'bottom':
                    moveY = elY - BBox.y2;
                    break;
            }
            const { scalex, scaley } = localMatrix.split();
            this.transform(localMatrix.translate(moveX / scalex, moveY / scaley));
            return this;
        };

        Element.prototype.pressEvent = function (callback) {
            var self = this;

            self.addClass('cp').mousedown(function (e) {
                var ev = e;
                callback.call(self, true, ev);
                self.mouseout(function () {
                    self.unmouseout();
                    self.unmouseup();
                    callback.call(self, false, ev);
                });
                self.mouseup(function () {
                    self.unmouseout();
                    self.unmouseup();
                    callback.call(self, false, ev);
                });
            });
            return self;
        };

        Element.prototype.getTBox = function () {
            const thisBBox = this.getBBox();
            const { a, b, c, d, e, f } = this.transform().totalMatrix;
            const local = this.transform().localMatrix;
            let x = e + ((thisBBox.x - local.e) * a) / local.a; //  x
            let y = f + ((thisBBox.y - local.f) * d) / local.d; //  x
            let width = (thisBBox.width * a) / local.a; // 
            let height = (thisBBox.height * d) / local.d; // 
            const cx = x + width / 2; //  x
            const cy = y + height / 2; //  y
            const x2 = x + width; //  x
            const y2 = y + height; //  y
            return { x, y, w: width, width, h: height, height, cx, cy, x2, y2, a, b, c, d, e, f };
        };

        Element.prototype.touchOrClick = function (callback) {
            this.addClass('cp').click(callback);
            return this;
        };

        Element.prototype.untouchOrClick = function (callback) {
            this.removeClass('cp').unclick(callback);
            return this;
        };

        Element.prototype.removeHint = function () {
            clearInterval(this.data('interval'));
            this.remove();
        };

        Element.prototype.center = function () {
            if (this.type === 'text') {
                this.addClass('ta-m');
                alignGroup({
                    g: this,
                    y: Number(this.attr('y'))
                });
            } else {
                this.anchor('center', 'middle');
            }
            return this;
        };
    });
})();

export default {
    SVG: Snap('#frame2d'), // SVG
    imageDefs: {}, // preload 
    frags: {},
    sounds: {}, // preload 
    modules: {}, // preload 
    filters: {},
    gradients: {},
    supportTouch, //  
    subjectCode: 'K1',
    controller: {
        addUserAction: Function,
        run: Function,
        next: Function,
        setTryCount: Function,
        initActions: Function
    } //    (ex.   )
};
