import { loadImage } from './image';
import { speaker } from '../assets/images';
import { loadVoice } from './sound';
import { Howler } from 'howler';

export default function tts({
    canvas,
    x,
    y,
    text,
    blinkOffset = { x: 0, y: 0 },
    locale = 'ko-KR',
    img = { src: speaker, key: 'speaker', type: 'svg' },
    startCallback,
    endCallback
}) {
    const g = canvas.g();
    const loaderG = g.g();
    const speakerImg = loadImage(img).use().appendTo(g).addClass('cp');
    let voice;
    g.click(async function () {
        if (startCallback) startCallback();
        speakerImg.attr({
            visibility: 'hidden'
        });
        for (let i = 0; i < 3; i++) {
            const circle = loaderG.circle(i * 15 + blinkOffset.x, 15 + blinkOffset.y, 5).addClass('speaker-color');
            setTimeout(circle.addClass.bind(circle), 100 * i, 'blink');
        }
        if (!voice) voice = await loadVoice(text.replace(/\$.+?\{(.+?)\}/g, '$1'), locale);
        loaderG.clear();
        speakerImg.attr({
            visibility: 'visible'
        });
        Howler.stop();
        voice.play();
        if (endCallback) voice.once('end', endCallback);
    });
    g.transform('t' + [x, y]);
    return g;
}
