import Snap from '../snap';
import SOL from './globalVariables';
import { cache } from './emotion';
export const promiseWrapper = func => new Promise(func);
export const sleep = (duration = 300) => new Promise(resolve => setTimeout(resolve, duration));
export const getSVGPoint = (x, y, svgEl = SOL.SVG.node) => {
    const pt = svgEl.createSVGPoint();
    (pt.x = x), (pt.y = y);
    const pos = pt.matrixTransform(svgEl.getScreenCTM().inverse());
    return {
        //  3   2018.12.18 Daniel
        x: Math.round(pos.x * 1e3) / 1e3,
        y: Math.round(pos.y * 1e3) / 1e3
    };
};
export const getPointAtEvent = (e, svgEl) => {
    const { pageX: x, pageY: y } = e;
    return getSVGPoint(x, y, svgEl);
};

export function getClosestPathLength(pathNode, point) {
    let pathLength = pathNode.getTotalLength(),
        precision = 8,
        bestLength,
        bestDistance = Infinity;
    const distance2 = ({ x, y }) => Math.pow(x - point.x, 2) + Math.pow(y - point.y, 2);
    // linear scan for coarse approximation
    for (let scanLength = 0, scanDistance; scanLength <= pathLength; scanLength += precision) {
        if ((scanDistance = distance2(pathNode.getPointAtLength(scanLength))) < bestDistance) {
            (bestLength = scanLength), (bestDistance = scanDistance);
        }
    }

    precision /= 2;
    while (precision > 0.5) {
        let beforeLength, afterLength, beforeDistance, afterDistance;
        if (
            (beforeLength = bestLength - precision) >= 0 &&
            (beforeDistance = distance2(pathNode.getPointAtLength(beforeLength))) < bestDistance
        ) {
            (bestLength = beforeLength), (bestDistance = beforeDistance);
        } else if (
            (afterLength = bestLength + precision) <= pathLength &&
            (afterDistance = distance2(pathNode.getPointAtLength(afterLength))) < bestDistance
        ) {
            (bestLength = afterLength), (bestDistance = afterDistance);
        } else {
            precision /= 2;
        }
    }

    return bestLength;
}

export function* getClassNames(classNames) {
    if (Array.isArray(classNames)) {
        for (const className of classNames) {
            yield className;
        }
    } else {
        for (let i = 0; i < Infinity; i++) {
            yield classNames;
        }
    }
}
export const getArrElements = getClassNames;
export const postMessage = data => {
    if (window !== window.parent) window.parent.postMessage(data, '*');
};

export const clearMain = () => {
    const main = document.getElementById('main');
    cache.sheet.flush();
    cache.inserted = {};
    cache.registered = {};
    let lastEl = main.lastChild;
    let firstEl = main.firstChild;
    while (lastEl !== SOL.SVG.node) {
        lastEl.remove();
        lastEl = main.lastChild;
    }
    while (firstEl !== SOL.SVG.node) {
        firstEl.remove();
        firstEl = main.firstChild;
    }
    SOL.SVG.clear();
};

export const makeShadow = ({ dx = 3, dy = 3, blur = 2, color = '#000000', opacity = 0.3, g = SOL.SVG } = {}) => {
    const key = dx + dy + blur + color + opacity;
    if (!SOL.filters[key]) SOL.filters[key] = g.filter(Snap.filter.shadow(dx, dy, blur, color, opacity));
    return SOL.filters[key].toDefs();
};
export const makeGradient = ({ type = 'l', coord = '(0,0,0,1)', color = '#fff-#000', g = SOL.SVG } = {}) => {
    const key = type + coord + color;
    if (!SOL.gradients[key]) SOL.gradients[key] = g.paper.gradient(key);
    return SOL.gradients[key].toDefs();
};
