
    import SOL from "../util/globalVariables"
    export const preloadModules = metaData => Promise.all(
        JSON.stringify(metaData).match(/(C|E|K|D)M\d{6}/g).map(( questionType ) =>
            import(`/Users/polaris/POLARIS/contents/src/modules/${questionType}`).then(r => {
                SOL.modules[questionType] = r.default;
            })
        )
    );
    export const loadModule = moduleType => SOL.modules[moduleType];
    