import SOL from './globalVariables';
import { clearMain, postMessage } from './util';
const forceNextModule = '00';
const showGrid = '01';
const showSpecificModule = '02';
const changeMetaData = '03';
const methods = {
    [forceNextModule]: ({ bool }) => SOL.controller.next({ moduleResult: bool }),
    [showGrid]: ({ bool }) => {
        if (bool) SOL.SVG.addClass('bg-grid');
        else SOL.SVG.removeClass('bg-grid');
    },
    [showSpecificModule]: ({ prevResult }) => {
        clearMain();
        SOL.controller.n = 0;
        if (!SOL.controller.originMetaData) SOL.controller.originMetaData = SOL.controller.metaData;
        SOL.controller.metaData = SOL.controller.originMetaData.filter((el, i) => prevResult[i]);
        postMessage({ code: '00', totalModuleCount: SOL.controller.metaData.length });
        SOL.controller.run();
    },
    [changeMetaData]: ({ metaData }) => {
        clearMain();
        SOL.controller.n = 0;
        SOL.controller.metaData = metaData;
        postMessage({ code: '00', totalModuleCount: metaData.length });
        SOL.controller.run();
    }
};
const receiveMessage = ({ data }) => {
    console.log('receiveMessage', data);
    methods[data?.code]?.(data);
};

export default receiveMessage;
