import 'core-js/stable';
import 'regenerator-runtime/runtime';
import SOL from './globalVariables';
import { createLoader, preloadMP3, preloadPNGImages, preloadSVGImages } from '../components';
import Snap from '../snap';
import { preloadModules } from './loadModule';
import { preloadFonts } from './text';
import { clearMain, postMessage, sleep } from './util';
import receiveMessage from './receiveMessage';
import { version } from '../package.json';
console.log('COMMON: v' + version);
export class ModuleProgress {
    constructor(metaData) {
        this.metaData = metaData;
    }
    tryCount = 3;
    actions = []; //0: 1:
    n = 0;
    nextFrameDelayTime = 300;
    setTryCount(count) {
        this.tryCount = count;
    }
    setNextFrameDelayTime(time) {
        this.nextFrameDelayTime = time;
    }
    addUserAction(action) {
        const { tryCount } = this;
        this.actions.push(action);
        const lastFeedbackActions = this.actions.filter(el => el === 0 || el === 1).slice(-tryCount);
        lastFeedbackActions.length >= tryCount &&
            lastFeedbackActions.every(el => el === 0) &&
            SOL.controller.next({ moduleResult: 0 });
    }
    initActions() {
        this.actions = [];
    }

    run() {
        const { metaData, n, next } = this;
        this.setTryCount(Number(metaData[n].tryCount) || 3);
        this.setNextFrameDelayTime(Number(metaData[n].nextFrameDelayTime) || 300);
        metaData[n].frame2dBackgroundWhite && SOL.SVG.addClass('bg-white');
        !metaData[n].frame2dBackgroundWhite && SOL.SVG.removeClass('bg-white');
        this.initActions();
        SOL.modules[metaData[n].questionType]({
            ...metaData[n],
            canvas: SOL.SVG,
            callback: next.bind(this)
        });
    }
    async next({ moduleResult = 1 } = {}) {
        let { metaData, run, nextFrameDelayTime } = this;
        await sleep(nextFrameDelayTime);
        postMessage({ code: '01', moduleIndex: this.n, moduleResult });
        this.n++;
        clearMain();
        if (this.n < metaData.length) {
            run.call(this);
        } else {
            console.log('!');
        }
    }
}

export default async function start(metaData, fonts, moduleProgress = ModuleProgress) {
    SOL.SVG = Snap('#frame2d');
    // SOL.subjectCode = process.env.SOL_FRAME_SUBJECT_CODE || 'K1';
    const loader = createLoader();
    preloadMP3(metaData);
    await Promise.all([
        preloadPNGImages(metaData),
        preloadSVGImages(metaData),
        preloadFonts(fonts),
        preloadModules(metaData)
    ]);
    loader.remove();
    postMessage({ code: '00', totalModuleCount: metaData.length });
    SOL.controller = new moduleProgress(metaData);
    window.addEventListener('message', receiveMessage);
    SOL.controller.run();
}
