import Snap from '../snap';
import { promiseWrapper } from '../util';
export const flip = function ({
    canvas,
    front,
    back,
    duration = 300,
    clickable = false,
    onFlipping,
    onFlipStart,
    onFlipEnd
}) {
    const flipCard = canvas.g();
    front.appendTo(flipCard);
    back.appendTo(flipCard);
    let isFlip = false;
    const planes = [front, back.attr({ opacity: 0 })];

    const bbox = planes.map(el => el.getBBox());
    flipCard.onFlipStart = onFlipStart;
    flipCard.onFlipEnd = onFlipEnd;
    flipCard.flip = () =>
        promiseWrapper(resolve => {
            {
                isFlip = !isFlip;
                if (flipCard.onFlipStart) flipCard.onFlipStart();
                Snap.animate(
                    0,
                    1,
                    val => {
                        planes[Number(isFlip)]
                            .transform(Snap.matrix(-1 + 2 * val, 0, 0, 1, (1 - val) * bbox[Number(!isFlip)].cx * 2, 0))
                            .attr({ opacity: Number(val >= 0.5) });
                        planes[Number(!isFlip)]
                            .transform(Snap.matrix(1 - 2 * val, 0, 0, 1, 2 * bbox[Number(isFlip)].cx * val, 0))
                            .attr({ opacity: Number(!(val >= 0.5)) });
                        if (onFlipping) onFlipping();
                    },
                    duration,
                    undefined,
                    () => {
                        flipCard.onFlipEnd?.();
                        resolve();
                    }
                );
            }
        });
    if (clickable) flipCard.click(() => flipCard.flip());

    return flipCard;
};

export const makeWriteAnimation = ({ fillPaths, strokePaths, canvas, speed = 0.05, x, y, scale }) => {
    const g = canvas.g();
    let animIndex = 0;
    fillPaths.forEach(el => g.path(el).addClass('f03'));
    const { storkePathElements, strokeLengths } = strokePaths.reduce(
        (p, pathStr, i) => {
            const clipPath = g.clipPath();
            const len = Snap.path.getTotalLength(pathStr);
            clipPath.path(fillPaths[i]);
            p.storkePathElements.push(
                g.path(pathStr).addClass('fno s00').attr({
                    strokeWidth: 18,
                    strokeDasharray: len,
                    strokeDashoffset: len,
                    clipPath,
                    strokeLinecap: 'round'
                })
            );
            p.strokeLengths.push(len);
            p.totalLength += len;
            return p;
        },
        { storkePathElements: [], strokeLengths: [], totalLength: 0 }
    );
    const onEnd = () => {
        animIndex++;
        if (animIndex === fillPaths.length) {
            animIndex = 0;
            storkePathElements.forEach((el, i) => el.attr({ strokeDashoffset: strokeLengths[i] }));
        }
        startAnim(onEnd);
    };
    startAnim(onEnd);
    function startAnim(onEnd) {
        const len = strokeLengths[animIndex];
        const el = storkePathElements[animIndex];
        Snap.animate(
            0,
            1,
            val => {
                el.attr({
                    strokeDashoffset: len * (1 - val)
                });
            },
            len / speed,
            undefined,
            onEnd
        );
    }
    g.transform(Snap.matrix(scale, 0, 0, scale, x, y)).center();
    return g;
};
