import { getClassNames } from '../util';

export default function text({
    text,
    x = 0,
    y = 0,
    canvas,
    className,
    fontSize,
    highlightClassNames,
    fill: _fill = ''
}) {
    const splitText = text.split(/\$highlight\{(.*?)\}/g);
    const textElement = canvas.text(x, y, splitText).addClass(className).attr({ fontSize });
    textElement.selectAll('tspan').forEach((el, i) => {
        if (i % 2 === 1) {
            const className = Array.isArray(highlightClassNames)
                ? highlightClassNames[(i - 1) / 2]
                : highlightClassNames;
            const fill = Array.isArray(_fill) ? _fill[(i - 1) / 2] : _fill;
            el.attr({ fill }).addClass(className);
        }
    });
    return textElement;
}
export const richText = ({
    canvas,
    fontSize = 15,
    className,
    text,
    dy = 40,
    highlightClassNames,
    underlineClassNames,
    blankClassNames = 'f14 s42',
    x,
    y,
    lineCenter = false,
    isSplit = false,
    boxMargin
}) => {
    const g = canvas.g();
    const margin = fontSize * 0.05;
    const lines = text.split('\n');
    const sentenses = [];
    let highlightClassName = getClassNames(highlightClassNames),
        blankClassName = getClassNames(blankClassNames),
        underlineClassName = getClassNames(underlineClassNames);
    for (let i = 0; i < lines.length; i++) {
        sentenses[i] = lines[i].replace(/\s/g, '\u00A0').split(/(\$[a-zA-z]+?\{.+?\})/);
        let newX = x;
        const newY = y + dy * i;
        const subG = g.g();
        for (let j = 0; j < sentenses[i].length; j++) {
            const word = _getWordByOption(sentenses[i][j]);
            const wordText = subG
                .text(newX, newY, isSplit ? word.word.split(/(\s{0,1}.+?\s{1})/).filter(Boolean) : word.word)
                .addClass(className)
                .attr({
                    'font-size': fontSize
                });
            let wordBBox;

            if (word.type === '$blank') {
                wordBBox = wordText.getBBox();
                wordText.remove();
                const { x, y, w, h } = wordBBox;
                const _margin = boxMargin || margin;
                subG.rect(x, y - _margin, w + _margin * 2, h + _margin * 2)
                    .addClass(blankClassName.next().value)
                    .attr({
                        'stroke-width': fontSize * 0.05
                    })
                    .data('text', word.word);
                newX = newX + 2 * _margin + margin;
            } else if (word.type === '$underline') {
                wordBBox = wordText.getBBox();
                const _lineY = wordBBox.y2 + margin;
                subG.line(newX, _lineY, newX + wordBBox.w, _lineY)
                    .addClass(underlineClassName.next().value)
                    .attr({ 'stroke-width': fontSize * 0.05 });
            } else if (word.type === '$highlight') {
                wordText.removeClass(className).addClass(highlightClassName.next().value);
            }

            newX = newX + (wordBBox || wordText.getBBox()).w;
        }
        if (lineCenter) subG.anchor('center');
    }
    function _getWordByOption(word) {
        const match = word.match(/\$(highlight|underline|blank)\{(.+)\}/);
        if (!match) return { word };
        return { type: '$' + match[1], word: match[2] };
    }

    return g;
};

export const richTextLineBreak = ({
    canvas,
    fontSize = 15,
    className,
    text,
    dy = 40,
    highlightClassNames,
    underlineClassNames,
    blankClassNames = 'f14 s42',
    x,
    y,
    lineCenter = false,
    boxMargin,
    textLength
}) => {
    const g = canvas.g();
    const margin = fontSize * 0.05;
    const lines = text.split('\n');
    let highlightClassName = getClassNames(highlightClassNames),
        blankClassName = getClassNames(blankClassNames),
        underlineClassName = getClassNames(underlineClassNames);
    const groups = lines.map((sentence, i) => {
        const newY = y + dy * i;
        const subG = g.g();
        const sentenceArr = sentence.replace(/\s/g, '\u00A0').split(/(\$[a-zA-z]+?\{.+?\})/);
        const words = sentenceArr.map(_getWordByOption);
        const { texts, types } = words.reduce(
            (p, c) => {
                let splitText = [c.word];
                if (c.type !== '$blank') splitText = c.word.split(/(\s{0,1}.+?\s{1})/).filter(Boolean);
                p.texts = p.texts.concat(splitText);
                p.types = p.types.concat(splitText.map(() => c.type));
                return p;
            },
            { texts: [], types: [] }
        );
        subG.text(x, newY, texts)
            .selectAll('tspan')
            .forEach((el, i) => {
                el.data('type', types[i]);
            });
        return subG;
    });

    const texts = Array.from(g.selectAll('text'));
    texts.forEach((text, i) => {
        const tspans = text.selectAll('tspan');
        for (let j = tspans.length - 1; j >= 0; j--) {
            const tspan = tspans[j];
            const bbox = tspan.getBBox();
            if (textLength && bbox.x2 > x + textLength) {
                if (groups[i + 1]) texts[i + 1].prepend(tspan);
                else {
                    groups[i + 1] = g.g();
                    texts[i + 1] = groups[i + 1].text(x, y + dy * texts.length).prepend(tspan);
                }
            } else break;
        }
    });
    handleLast();
    function handleLast() {
        const text = texts.last;
        const len = texts.length;
        const bbox = text.getBBox();
        console.log(bbox.w);
        if (textLength && bbox.w > textLength) {
            const tspans = text.selectAll('tspan');
            groups[len] = g.g();
            texts[len] = groups[len].text(x, y + dy * len);
            for (let j = tspans.length - 1; j >= 0; j--) {
                const tspan = tspans[j];
                const bbox = tspan.getBBox();
                if (textLength && bbox.x2 > x + textLength) {
                    texts[len].prepend(tspan);
                } else break;
            }
            handleLast();
        }
    }
    const tspans = g.selectAll('tspan');
    g.selectAll('tspan').forEach((el, i) => {
        if (el.data('type') === '$highlight') {
            el.removeClass(className).addClass(highlightClassName.next().value);
        }
        const wordBBox = el.getBBox();
        const g = el.parent().parent();
        if (el.data('type') === '$underline') {
            const _lineY = wordBBox.y2 + margin;
            g.line(wordBBox.x, _lineY, wordBBox.x2, _lineY)
                .addClass(underlineClassName.next().value)
                .attr({ 'stroke-width': fontSize * 0.05 });
        }
        if (el.data('type') === '$blank') {
            console.log(el.parent());
            const { x, y, w, h } = wordBBox;
            const _margin = boxMargin || margin;
            g.rect(x, y - _margin, w + _margin * 2, h + _margin * 2)
                .addClass(blankClassName.next().value)
                .attr({
                    'stroke-width': fontSize * 0.05
                })
                .data('text', el.attr('text'));
            if (tspans[i + 1]) tspans[i + 1].attr({ dx: 2 * _margin + margin });
        }
    });
    if (lineCenter) g.selectAll('g').forEach(el => el.anchor('center'));
    function _getWordByOption(word) {
        const match = word.match(/\$(highlight|underline|blank)\{(.+)\}/);
        if (!match) return { word };
        return { type: '$' + match[1], word: match[2] };
    }

    return g;
};
