import { SOL } from '.';

export const getYForMiddle = (midY, font, fs) => {
    let offset;
    switch (font) {
        case 'nm':
        case 'mc':
        case 'dg':
            offset = fs / 8;
            break;
        case 'gt':
            offset = fs / 6;
            break;
        default:
            offset = fs / 5;
            break;
    }
    return midY + fs / 2 - offset;
};

const preloadFont = font =>
    new Promise(resolve => {
        const fontClass = font.className || font;
        const test = font.test || 'abcdefghijklmnopqrstuvwxyz0123456789+-*/';
        const text = SOL.SVG.text(1000, 1000, test);
        const initialWidth = text.getBBox().w;
        const interval = setInterval(checkFont);
        text.addClass(fontClass);
        function checkFont() {
            if (initialWidth !== text.getBBox().w) {
                clearInterval(interval);
                text.remove();
                console.log(`FONT:${fontClass} load complete`);
                resolve();
            }
        }
    });

export const preloadFonts = (fontClasses = []) => Promise.all(fontClasses.map(preloadFont));
