import { Howl } from 'howler';
import { SOL } from '../util';
import { axios } from '../util/axios';

export const loadLocalSound = url => {
    const key = url.replace(/[a-zA-z0-9:]*\/|\.mp3/g, '');
    if (SOL.sounds[key]) return SOL.sounds[key];
    SOL.sounds[key] = new Howl({
        src: [url]
    });
    return SOL.sounds[key];
};

export const loadSound = url => loadLocalSound(`${process.env.SOL_FRAME_LCMS}/${url}`);

export const loadVoice = async (text, locale) => {
    console.log('locale: ' + locale || 'ko-KR');
    const audioData = (
        await axios.post(`/learning/tts`, {
            text,
            language: locale || 'ko-KR',
            gender: 'FEMALE'
        })
    ).data;
    return new Howl({
        src: [audioData]
    });
};
export const preloadMP3 = metaData => {
    const mp3List = JSON.stringify(metaData).match(/[a-zA-z0-9/]+\.mp3/g) || [];
    mp3List.forEach(el => {
        loadSound(el);
    });
};
