import Snap from '../snap';

import { SOL } from '../util';
import { axios } from '../util/axios';
export const getImageURLFromMetaData = (metaData, type = 'svg') =>
    (JSON.stringify(metaData).match(new RegExp(`[a-zA-z0-9/]+\\.${type}`, 'g')) || []).filter(
        (el, i, arr) => arr.indexOf(el) === i
    );
export const makeParameterForPreLoadImage = str => ({
    url: str,
    key: str.replace(/[a-zA-z0-9:]*\/|\.(png|svg)/g, ''),
    type: str.match(/\.(png|svg)/g)[0].replace('.', ''),
    src: str
});
export const loadImage = ({ src, width = 0, height = 0, key, type, rx, ry }) => {
    if (!SOL.imageDefs[key]) {
        const defsG = SOL.SVG.g();
        SOL.imageDefs[key] = defsG;
        if (type === 'svg') {
            const frag = SOL.frags[key] || loadFragment({ src, key });
            defsG.append(frag);
            width =
                frag.node.getAttribute('width')?.replace('px', '') || frag.node.getAttribute('viewBox').split(' ')[2];
            height =
                frag.node.getAttribute('height')?.replace('px', '') || frag.node.getAttribute('viewBox').split(' ')[3];
            defsG.rect(0, 0, width, height).attr({ opacity: 0 });
        } else {
            defsG.image(src, 0, 0, width, height);
        }
    }
    if (width) SOL.imageDefs[key].width = width;
    if (height) SOL.imageDefs[key].height = height;

    return SOL.imageDefs[key].toDefs();
};
const loadFrags = arr =>
    arr.map(({ key, src }) => {
        SOL.frags[key] = loadFragment({ src, key });
    });
export const loadImages = arr => arr.map(el => loadImage(el));
export const preloadSVGImages = async meta => {
    const arr = getImageURLFromMetaData(meta, 'svg').map(makeParameterForPreLoadImage);
    const svgSources = await Promise.all(arr.map(img => axios.get(`/${img.url}`)));
    const svgImages = svgSources.map((src, i) => ({ ...arr[i], src: src.data }));
    loadFrags(svgImages);
};

export const loadPNG = (r, url) => {
    const dataView = new DataView(r.data, 0, 28);
    const width = dataView.getInt32(16);
    const height = dataView.getInt32(20);
    const blob = new Blob([r.data], { type: r.headers['content-type'] });
    const imgUrl = URL.createObjectURL(blob);
    return loadImage({ ...makeParameterForPreLoadImage(url), width, height, src: imgUrl });
};

export const preloadPNG = async url => {
    const r = await axios.get(`/${url}`, { responseType: 'arraybuffer' });
    return loadPNG(r, url);
};

export const preloadPNGImages = async meta => {
    const arr = getImageURLFromMetaData(meta, 'png');
    return await Promise.all(arr.map(preloadPNG));
};
export const loadFragment = ({ src, url, key }) => {
    if (src) return Snap.parse(src.match(/<svg(.|\n|\s)+<\/svg>/g)[0].replace(/st(\d+)/g, key + '$1'));
    if (url) return SOL.frags[makeParameterForPreLoadImage(url).key];
};
export default function image({ canvas, x = 0, y = 0, img, center }) {
    const g = canvas.g();
    const { url, rx, ry } = img;
    const key = url.replace(/[a-zA-z0-9:]*\/|\.(png|svg)$/g, '');
    let type = SOL.frags[key] && url.match(/\.(png|svg)$/g)[0].replace('.', '');
    let src = url;
    let imgEl = loadImage({ ...img, key, type, src })[type === 'svg' ? 'use' : 'clone']();
    if (rx || ry) {
        const { width, height } = SOL.imageDefs[key];
        const clipPath = g.clipPath();
        clipPath.rect(0, 0, width, height, rx, ry);
        imgEl.attr({ clipPath });
    }
    imgEl.appendTo(g).transform(Snap.matrix(img.scale || 1, 0, 0, img.scale || 1, x, y));
    if (center) {
        imgEl.center();
    }
    return g;
}
