const getSvgViewBox = () => {
    var svgViewBox = {
        width: 800,
        height: 500
    };
    var ratio = window.innerWidth / window.innerHeight;
    if (ratio > 1.333) svgViewBox.width = svgViewBox.height * ratio;
    else svgViewBox.height = svgViewBox.width / ratio;
    return svgViewBox;
};

const svgToScreen = svg => {
    const screen = {};
    const basicSvg = document.getElementById('frame2d');
    const screenCTM = basicSvg.getScreenCTM();
    if (!svg) {
        const svgViewBox = getSvgViewBox();
        svg = {
            x: 0,
            y: 0,
            width: svgViewBox.width,
            height: svgViewBox.height
        };
    }
    if (svg.x !== undefined && svg.y !== undefined) {
        const svgPoint = basicSvg.createSVGPoint();
        svgPoint.x = svg.x;
        svgPoint.y = svg.y;
        const screenPoint = svgPoint.matrixTransform(screenCTM);
        screen.x = screenPoint.x;
        screen.y = screenPoint.y;
    }
    if (svg.width !== undefined && svg.height !== undefined) {
        screen.width = svg.width * screenCTM.a;
        screen.height = svg.height * screenCTM.d;
    }
    return screen;
};

export const changeScrollGroup = (element, info) => {
    const scrollRect = {
        x: info.transform[0],
        y: info.transform[1],
        width: info.width,
        height: 500 - info.transform[1]
    };
    const contentsSize = {
        width: info.width,
        height: info.height
    };
    const screenDivRect = svgToScreen(scrollRect);
    const screenSvgSize = svgToScreen(contentsSize);
    element.div.style.left = screenDivRect.x + 'px';
    element.div.style.top = screenDivRect.y + 'px';
    element.div.style.width = screenDivRect.width + 'px';
    element.div.style.height = screenDivRect.height + 'px';
    if (info.background) element.div.style.background = info.background;

    element.svg.setAttribute('width', screenSvgSize.width + 'px');
    element.svg.setAttribute('height', screenSvgSize.height + 'px');
    element.svg.setAttribute('viewBox', '0 0 ' + contentsSize.width + ' ' + contentsSize.height);
    return {
        g: element.snapSvg.g(),
        transform: [scrollRect.x, scrollRect.y],
        width: scrollRect.width,
        height: scrollRect.height
    };
};
